@import 'mixin';
@import 'variables';

.popup {
	$ItemSize: calculateRem(64px);

	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.5);
	text-align: center;
	z-index: 1;
	opacity: 0;
	@include transition(all .1s linear);
	@include ElementVerticleAlignMiddle();
	&.show { 
		display: block; 
		opacity: 1;
		z-index: 10;
		overflow-y: scroll;
	}
	&.reCall .text {
		width: 70%;
		margin: 0 auto calculateRem(16px) 30%;
	}
	
	$type: true, false;
	@for $i from 1 through 2 {
		&.#{nth($type, $i)} {
			.content:before {
				content: '';
				display: block;
				background-image: url("../images/#{nth($type, $i)}.png");
				width: 80%;
				height: 80%;
				position: absolute;
				background-position: center;
				background-repeat: no-repeat;
				background-size: contain;
				top: 0; bottom: 0; left: 0; right: 0;
				margin: auto;
			}
		}
	}
	
	.content {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		background: rgba(#F1F1F1, .95);
		width: 60%;
		margin: 0 auto;
		padding: calculateRem(40px) calculateRem(20px) calculateRem(20px);
		overflow: hidden;
		@include box-shadow(5px 5px 10px rgba(0,0,0,.5));
		@include border-radius(4px);
		
		.relative {
			position: relative;
			z-index: 2;
		}
		.text {
			font-family: 'w3';
			font-size: calculateRem(14px);
			text-align: justify;
			line-height: 1.6;
			width: 80%;
			margin: 0 auto calculateRem(16px) auto;
		}

		.buttons {
			button {
				font-family: 'w5';
				width: $ItemSize;
				height: $ItemSize;
				color: white;
				font-size: calculateRem(13px);
				cursor: pointer;
				display: none;
				@include font-size(12px);
				@include border-radius(50%);
				@include box-shadow(0 0 3px rgba(0,0,0,.5));
				@include ElementVerticleAlignMiddle();
				&.show { display: inline-block; }
				.texts {
					display: inline-block;
					vertical-align: middle;
					div { 
						line-height: 1.3;
						letter-spacing: .8px; 
					}
				}
				&.green {
					background: $ButtonGreen;
					&:hover,.click { background: darken($ButtonGreen, 5); }
				}
				&.red {
					background: $ButtonRed;
					&:hover, .click { background: darken($ButtonRed, 5); }
				}
				&+button {
					margin-left: calculateRem(12px);
				}
			}
		}

		.background {
			position: absolute;
			z-index: 1;
			width: 100%;
			height: 100%;
			top: 0; bottom: 0; left: 0; right: 0;
			margin: auto;
			text-align: center;
			i {
				position: absolute;
				top: 0;
				left: 0;
				left: 0;
				right: 0;
				margin: auto;
				display: none;
				color: white;
				opacity: .6;
				font-size: 40vw;
				margin-top: 2.5rem;
			}
		}
	}

	@media only screen and (min-width: 1440px) {
		.content {
			width: 24%;
			@include border-radius(15px);
			@include box-shadow(5px 5px 20px rgba(0,0,0,.7));
			.text {
				@include font-size(18px);
			}
			.buttons button {
				@include font-size(16px);
			}
		}
	}
}